import "./topbar.css";
import SearchIcon from "@mui/icons-material/Search";
import HomeIcon from "@mui/icons-material/Home";
import LiveTvIcon from "@mui/icons-material/LiveTv";
import StorefrontIcon from "@mui/icons-material/Storefront";
import Diversity1Icon from "@mui/icons-material/Diversity1";
import GamesIcon from "@mui/icons-material/Games";
import MenuOpenIcon from "@mui/icons-material/MenuOpen";
import MarkUnreadChatAltIcon from "@mui/icons-material/MarkUnreadChatAlt";
import NotificationsActiveIcon from "@mui/icons-material/NotificationsActive";
import Profile from "../../assets/people/1.jpg";

const Topbar = () => {
  return (
    <>
      <div className="topBar">
        <div className="top-left">
          <h1>BB</h1>
          <div className="search">
            <SearchIcon className="searchIcon" />
            <input type="text" placeholder="Search BammyBook" />
          </div>
        </div>

        <div className="top-center">
          <div>
            <HomeIcon className="icons" />
            <span>Home</span>
          </div>
          <div>
            <LiveTvIcon className="icons" />
            <span>Watch</span>
          </div>
          <div>
            <StorefrontIcon className="icons" />
            <span>Marketplace</span>
          </div>
          <div>
            <Diversity1Icon className="icons" />
            <span>Communities</span>
          </div>
          <div>
            <GamesIcon className="icons" />
            <span>Gaming</span>
          </div>
        </div>

        <div className="top-right">
          <div>
            <div className="icons">
              <MenuOpenIcon />
            </div>
            <span>Menu</span>
          </div>
          <div>
            <div className="icons">
              <MarkUnreadChatAltIcon />
            </div>
            <span>Messenger</span>
          </div>
          <div>
            <div className="icons">
              <NotificationsActiveIcon />
            </div>
            <span>Notifications</span>
          </div>
          <div>
            <img src={Profile} alt="" className="icons" />
            <span>Profile</span>
          </div>
        </div>
      </div>
    </>
  );
};

export default Topbar;
