import "./upload.css";
import pp1 from "../../assets/people/1.jpg";
import CameraAltIcon from "@mui/icons-material/CameraAlt";
import CollectionsIcon from "@mui/icons-material/Collections";
import InsertEmoticonIcon from "@mui/icons-material/InsertEmoticon";

const Upload = () => {
  return (
    <div className="upload">
      <div className="uploadPost">
        <div className="uploadTop">
          <div>
            <img src={pp1} alt="" />
          </div>
          <input type="text" placeholder="What's on your mind, NurseDup" />
        </div>
        <hr />
        <div className="uploadBottom">
          <div>
            <CameraAltIcon className="uploadIcons" />
            <span>Live video</span>
          </div>
          <div>
            <CollectionsIcon className="uploadIcons" />
            <span>Photo/video</span>
          </div>
          <div>
            <InsertEmoticonIcon className="uploadIcons" />
            <span>Feeling/activity</span>
          </div>
        </div>
      </div>

      <div className="modal">
        
      </div>
    </div>
  );
};

export default Upload;
