import "./yourpages.css";
import LinearScaleIcon from "@mui/icons-material/LinearScale";
import CodeOffIcon from "@mui/icons-material/CodeOff";
import MessageIcon from "@mui/icons-material/Message";
import NotificationsNoneIcon from "@mui/icons-material/NotificationsNone";
import CampaignIcon from "@mui/icons-material/Campaign";

const Yourpages = () => {
  return (
    <>
      <div className="pagesTop">
        <h3>Your Pages and Profile</h3>
        <LinearScaleIcon className="icon" />
      </div>

      <div className="pagesLogoBox">
        <div className="pagesLogo">
          <CodeOffIcon />
        </div>
        <h4>devBammy</h4>
      </div>

      <ul>
        <li>
          <MessageIcon className="icon" />
          <span>12 Messages</span>
        </li>
        <li>
          <NotificationsNoneIcon className="icon" />
          <span>21 Notifications</span>
        </li>
        <li>
          <CampaignIcon className="icon" />
          <span>21 Create Promotions</span>
        </li>
      </ul>
    </>
  );
};

export default Yourpages;
