import "./sidebar.css";
import { useState } from "react";
import Profile from "../../assets/people/1.jpg";
import PeopleAltIcon from "@mui/icons-material/PeopleAlt";
import Diversity3Icon from "@mui/icons-material/Diversity3";
import StorefrontIcon from "@mui/icons-material/Storefront";
import DesktopWindowsIcon from "@mui/icons-material/DesktopWindows";
import AccessTimeFilledIcon from "@mui/icons-material/AccessTimeFilled";
import BookmarkIcon from "@mui/icons-material/Bookmark";
import FlagIcon from "@mui/icons-material/Flag";
import EventBusyIcon from "@mui/icons-material/EventBusy";
import TodayIcon from "@mui/icons-material/Today";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import ExpandLessIcon from "@mui/icons-material/ExpandLess";
import CampaignIcon from "@mui/icons-material/Campaign";
import BarChartIcon from "@mui/icons-material/BarChart";
import SpaIcon from "@mui/icons-material/Spa";
import VolunteerActivismIcon from "@mui/icons-material/VolunteerActivism";
import CoronavirusIcon from "@mui/icons-material/Coronavirus";
import FavoriteIcon from "@mui/icons-material/Favorite";
import CreditCardIcon from "@mui/icons-material/CreditCard";
import VideogameAssetIcon from "@mui/icons-material/VideogameAsset";
import CollectionsIcon from "@mui/icons-material/Collections";
import LogoDevIcon from "@mui/icons-material/LogoDev";

const Sidebar = () => {
  const [show, setShow] = useState(false);

  return (
    <div className="sideBar">
      <ul className="sideLists">
        <li className="listItem">
          <div className="profile">
            <img src={Profile} alt="" />
          </div>
          <span className="profileName">NurseDup 001</span>
        </li>
        <li className="listItem">
          <PeopleAltIcon className="sideIcons" />
          <span>Friends</span>
        </li>
        <li className="listItem">
          <Diversity3Icon className="sideIcons" />
          <span>Community</span>
        </li>
        <li className="listItem">
          <StorefrontIcon className="sideIcons" />
          <span>Marketplace</span>
        </li>
        <li className="listItem">
          <DesktopWindowsIcon className="sideIcons" />
          <span>Watch</span>
        </li>
        <li className="listItem">
          <AccessTimeFilledIcon className="sideIcons" />
          <span>Memories</span>
        </li>
        <li className="listItem">
          <BookmarkIcon className="sideIcons" />
          <span>Saved</span>
        </li>
        <li className="listItem">
          <FlagIcon className="sideIcons" />
          <span>Pages</span>
        </li>
        <li className="listItem">
          <EventBusyIcon className="sideIcons" />
          <span>Events</span>
        </li>
        <li className="listItem">
          <TodayIcon className="sideIcons" />
          <span>Most Recent</span>
        </li>
      </ul>

      {show ? (
        <div id="more">
          <li className="listItem">
            <CampaignIcon className="sideIcons" />
            <span>Ads Center</span>
          </li>
          <li className="listItem">
            <BarChartIcon className="sideIcons" />
            <span>Ads Manager</span>
          </li>
          <li className="listItem">
            <SpaIcon className="sideIcons" />
            <span>Climate Science Centre</span>
          </li>
          <li className="listItem">
            <VolunteerActivismIcon className="sideIcons" />
            <span>Community Help</span>
          </li>
          <li className="listItem">
            <CoronavirusIcon className="sideIcons" />
            <span>COVID-19 information Centre</span>
          </li>
          <li className="listItem">
            <FavoriteIcon className="sideIcons" />
            <span>Emotional Health</span>
          </li>
          <li className="listItem">
            <CreditCardIcon className="sideIcons" />
            <span>Bammy Pay</span>
          </li>
          <li className="listItem">
            <VideogameAssetIcon className="sideIcons" />
            <span>Video Game</span>
          </li>
          <li className="listItem">
            <CollectionsIcon className="sideIcons" />
            <span>Recent Ad Activity</span>
          </li>
        </div>
      ) : null}

      {!show ? (
        <button onClick={() => setShow(!show)} className="seeMoreBtn">
          <ExpandMoreIcon /> See More
        </button>
      ) : (
        <button onClick={() => setShow(!show)} className="seeMoreBtn">
          <ExpandLessIcon /> See Less
        </button>
      )}

      <hr />

      <div className="shortcuts">
        <h3>Your Shortcuts</h3>
        <li className="listItem short">
          <LogoDevIcon className="sideIcons" />
          <p>devBammy</p>
        </li>
      </div>

      <a href="https://github.com/devbammy" target="_blank" rel="noreferrer">
        devBammy  2022
      </a>
    </div>
  );
};

export default Sidebar;
