import "./reels.css";
import reel1 from "../../assets/reels/1.jpg";
import reel2 from "../../assets/reels/2.jpg";
import reel3 from "../../assets/reels/3.jpg";
import reel4 from "../../assets/reels/4.jpg";
import reel5 from "../../assets/reels/5.jpg";
import reel6 from "../../assets/reels/6.jpg";
import reel7 from "../../assets/reels/7.jpg";
import reel8 from "../../assets/reels/8.jpg";
import reel9 from "../../assets/reels/9.jpg";
import reel10 from "../../assets/reels/10.jpg";
import pp1 from "../../assets/people/1.jpg";
import pp2 from "../../assets/people/2.jpg";
import pp3 from "../../assets/people/3.jpg";
import pp4 from "../../assets/people/4.jpg";
import pp5 from "../../assets/people/5.jpg";
import pp6 from "../../assets/people/6.jpg";
import pp7 from "../../assets/people/7.jpg";
import pp8 from "../../assets/people/8.jpg";
import pp9 from "../../assets/people/9.jpg";
import pp10 from "../../assets/people/10.jpg";

import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";

const Reels = () => {
  const data = [
    {
      id: 1,
      image: reel1,
      name: "Mary Jane",
      pPhoto: pp1,
    },
    {
      id: 2,
      image: reel2,
      name: "Clara Moe",
      pPhoto: pp2,
    },
    {
      id: 3,
      image: reel3,
      name: "John Doe",
      pPhoto: pp3,
    },
    {
      id: 4,
      image: reel4,
      name: "Robert Stance",
      pPhoto: pp4,
    },
    {
      id: 5,
      image: reel5,
      name: "Music Man",
      pPhoto: pp5,
    },
    {
      id: 6,
      image: reel6,
      name: "Phone Girl",
      pPhoto: pp6,
    },
    {
      id: 7,
      image: reel7,
      name: "Tablet Lady",
      pPhoto: pp7,
    },
    {
      id: 8,
      image: reel8,
      name: "Full Hair",
      pPhoto: pp8,
    },
    {
      id: 9,
      image: reel9,
      name: "Skate Board",
      pPhoto: pp9,
    },
    {
      id: 10,
      image: reel10,
      name: "Happy Girl",
      pPhoto: pp10,
    },
  ];

  return (
    <div className="reelsContainer">
      <div className="reels">
        <div className="top">
          <button className="active">Stories</button>
          <button>Reels</button>
          <button>Rooms</button>
        </div>
        <div className="bottom">
          <Swiper
            slidesPerView={5}
            spaceBetween={10}
            pagination={{
              clickable: false,
            }}
            className="mySwiper"
          >
            {data.map((reel) => (
              <SwiperSlide className="reelPix" key={reel.id}>
                <img src={reel.image} alt={reel.name} />
                <div className="pp">
                  <img src={reel.pPhoto} alt={reel.name} />
                </div>
                  <p>{reel.name}</p>
              </SwiperSlide>
            ))}
          </Swiper>
        </div>
      </div>
    </div>
  );
};

export default Reels;
